<template>
  <div class="mineself">
    <router-view class="personalsettings" :gets="getmoneyinfo"></router-view>
    <Titletop title="个人中心">
      <router-link slot="l" replace :to="{ name: 'persion' }"></router-link>
      <router-link slot="r" replace to="/index"></router-link>
    </Titletop>
    <div class="personal">
      <div class="usertop">
        <div class="userbasic">
          <img
            src="https://m.jyacg.com/static/img/userheader.f76c487.png"
            v-show="!userapp.avatar"
          />
          <img :src="userapp.avatar" v-show="userapp.avatar" />
          <div>
            <h4>{{ userapp.nickname }}</h4>
            <h4>性别: {{ getsex }}</h4>
            <span>LV{{ Info.students }}</span>
          </div>
        </div>
        <div class="usermoney">
          <div>
            <img src="@/assets/img/yb.png" /><span
              >书币{{ userapp.book_coin }}</span
            >
          </div>
          <button><router-link :to="{name:'topup'}">充值</router-link></button>
        </div>
        <div class="userfocus">
          <div>
            <span>{{ Info.concerns }}</span>
            <p>关注</p>
          </div>
          <div>
            <span>{{ Info.students }}</span>
            <p>粉丝</p>
          </div>
        </div>
      </div>
      <div class="userbottom">
        <router-link :to="{name:'persion'}">
          <div>
            <img src="@/assets/img/sj.png" />
            <h4>我的书架</h4>
          </div>
          <span>&gt;</span>
        </router-link>
        <router-link to="">
          <div>
            <img src="@/assets/img/zd.png" />
            <h4>我的账单</h4>
          </div>
          <span>&gt;</span>
        </router-link>
        <router-link :to="{ name: 'mysics' }">
          <div>
            <img src="@/assets/img/sz.png" />
            <h4>个人设置</h4>
          </div>
          <span>&gt;</span>
        </router-link>
      </div>
    </div>
    <van-button round type="info" size="large" @click="Logout"
      >退出登录</van-button
    >
  </div>
</template>

<script>
import Titletop from "@/components/Titletop.vue";
export default {
  name: "Mymineself",
  components: { Titletop },

  data() {
    return {
      userapp: JSON.parse(localStorage.getItem("userapp")),
      Info: "",
    };
  },
  methods: {
    Logout() {
      localStorage.removeItem("api_token");
      localStorage.removeItem("userapp");
      this.$router.replace({
        name: "login",
      });
    },
    getmoneyinfo(){
      this.axios
        .get("https://jyapi.jyacg.com/admin/user/moneyinfo", {
          headers: {
            authorization: localStorage.getItem("api_token"),
          },
        })
        .then((res) => {
          if (res.status == 200) {
            if (res.data.code == 0) {
              localStorage.setItem("userapp", JSON.stringify(res.data.data));
              this.userapp = res.data.data;
            } else if (res.data.code == 1002) {
              if (res.data.msg == "账号验证失效，请重新登录") {
                localStorage.removeItem("api_token");
                this.$dialog({
                  title: "温馨提示",
                  message: res.data.msg,
                }).then(() => {
                  this.$router.replace({
                    name: "login",
                  });
                });
              }
            }
          }
        });
    },
  },
  computed: {
    getsex() {
      return this.userapp.sex == 0
        ? "保密"
        : this.userapp.sex == 1
        ? "男"
        : this.userapp.sex == 2
        ? "女"
        : "";
    },
  },
  mounted() {
    this.getmoneyinfo();
    this.axios
      .get("https://jyapi.jyacg.com/admin/user/personcenter", {
        headers: {
          authorization: localStorage.getItem("api_token"),
        },
      })
      .then((res) => {
        this.Info = res.data.data;
      });
  },
};
</script>

<style lang="scss" scoped>
.personalsettings {
  position: fixed;
  display: block;
  width: 100%;
  height: 100%;
  background-color: #f4f3f1;
  z-index: 1;
}
.van-button {
  width: 304px;
  height: 44px;
  margin: 0px 35px;
  margin-top: 40px;
  background-color: #439fff;
}
.personal {
  width: 317px;
  height: 415px;
  margin: 0px 12.5px;
  margin-top: 16px;
  padding: 0px 11px 20px 21px;
  background-color: #fff;
  border-radius: 25px;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.1);
  .usertop {
    width: 317px;
    padding: 22px 0px 20px 0px;
    border-bottom: 3px solid #eee;
    .userbasic {
      border-bottom: 1px solid #eee;
      padding-bottom: 14.5px;
      height: 75px;
      margin-top: 11px;
      display: flex;
      img {
        height: 75px;
        width: 75px;
        margin-right: 20px;
        border-radius: 50%;
      }
      div {
        height: 75px;
        h4 {
          font-size: 18px;
          color: #333;
          margin: 7px 0;
          font-weight: bold;
        }
        h4:nth-child(2) {
          font-size: 15px;
        }
        span {
          border-radius: 10px;
          display: block;
          width: 50px;
          height: 16px;
          color: #fff;
          background-color: #b98d7c;
          font-size: 12px;
          text-align: center;
          line-height: 16px;
        }
      }
    }
    .usermoney {
      height: 50.5px;
      width: 317px;
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      padding-bottom: 7px;
      margin-bottom: 15px;
      div {
        width: 77px;
        display: flex;
        justify-content: space-between;
        img {
          height: 16px;
          width: 16px;
        }
        span {
          font-size: 18px;
          font-weight: bold;
          color: #333;
        }
      }
      button {
        width: 93px;
        height: 28px;
        border: 1px solid #439fff;
        background-color: #fff;
        padding: 0px;
        border-radius: 25px;
        a {
          font-weight: bold;
          font-size: 16px;
          color: #439fff;
        }
      }
    }
    .userfocus {
      width: 317px;
      height: 39px;
      display: flex;
      justify-content: space-around;
      div {
        width: 39px;
        text-align: center;
        span,
        p {
          font-weight: bold;
        }
        span {
          font-size: 20px;
          color: #333;
          display: block;
          height: 28px;
        }
        p {
          height: 19px;
          font-size: 15px;
          color: #666;
        }
      }
    }
  }
  .userbottom {
    width: 317px;
    a {
      width: 317px;
      height: 45px;
      margin: 10px 0px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      div {
        width: 105px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        h4 {
          font-size: 15px;
          color: #333;
          font-weight: bold;
        }
        img {
          width: 24px;
          height: 24px;
        }
      }
      span {
        font-size: 20px;
        line-height: 20px;
        color: #999;
        opacity: 0.6;
      }
    }
  }
}
</style>